.card {
	display: flex;
	flex-flow: row wrap;
	margin-top: 1rem;
	height: 8rem;
	background-color: burlywood;
	color: green;
	border: 0.1rem coral solid;
	border-radius: 1rem;
	cursor: pointer;
}

.card__number {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	text-align: center;
	font-size: 3rem;
	font-weight: 800;
	opacity: 0;
}

@keyframes card {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}

}

.back {
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	transition: 1s;
	backface-visibility: hidden;
	background-color: #fff;
	font-size: 3rem;
	font-weight: 800;
	opacity: 1;

}

.bad {
	color: red;
	opacity: 1;
}

.statistic {
	padding-top: 1rem;
	margin: 0 auto;
	text-align: center;
	font-size: 2rem;
}

@media screen and (max-width: 768px) {
	.statistic {
		font-size: 1.5rem;
	}

}